    const storageHandler = async () => {
      const store = new Storage();
      await store.create();
      const token = await store.get("token");
      if (token) {
        this.$router.push("/tabs/personalAccounts");
      } else {
        this.$router.push("/authPage")
      }
    };
    // registerNotifications()
    storageHandler();
